import { useEffect, useState } from "react";
import styles from "./SettingPage.module.scss";
import { AuthInput } from "components/AuthInput";
import { getProfile, updateProfile } from "api/userinfo";
import { useAuth } from "context/authContext";
import { useNavigate } from "react-router";
import { useCurrentUser } from "context/userInfoContext";
import clsx from "clsx";

export const SettingPage = () => {
  const [user, setUser] = useState({});
  const [nameLength, setNameLength] = useState("");
  const { isAuthenticated } = useAuth();
  const { profile, setProfile } = useCurrentUser();
  const navigate = useNavigate();
  const id = localStorage.getItem("id");
  useEffect(() => {
    if (!isAuthenticated) {
      navigate("/login");
      return;
    } else return;
  }, [isAuthenticated, navigate]);

  useEffect(() => {
    const showPage = async () => {
      setProfile(await getProfile(id));
      // setNameLength(profile.name.length);
    };
    showPage();
    console.log(profile.name);
    console.log("3600 test from setting page");
  }, [id, setProfile, profile.name]);

  const handleSave = async () => {
    if (user.account === "") {
      alert("");
      return;
    }
    if (user.name === "") {
      alert("");
      return;
    }
    if (user.email === "") {
      alert("Email");
      return;
    }
    if (user.password !== user.checkPassword) {
      alert("");
      return;
    }
    if (nameLength > 50) {
      alert("50");
      return;
    }
    console.log(user);
    const res = await updateProfile(user);
    console.log(res);
    if (res) {
      alert("");
      const refresh = () => window.location.reload(true);
      refresh();
    }
  };
  return (
    <>
      <div className={styles.mainbarContainer}>
        <header className={styles.settingPageHeader}>
          <h1 className={styles.settingPageTitle}></h1>
        </header>

        <div className={styles.inputContainer}>
          <AuthInput
            type="text"
            label=""
            value={profile.account}
            onChange={(e) => {
              setUser({
                ...user,
                account: e.target.value,
              });
            }}
          />
        </div>
        <div className={styles.inputContainer}>
          <AuthInput
            type="text"
            label=""
            value={profile.name}
            onChange={(e) => {
              setNameLength(e.target.value.length);
              setUser({
                ...user,
                name: e.target.value,
              });
            }}
          />
          <span
            className={clsx("", {
              [styles.limit]: nameLength > 50,
              [styles.noLimit]: nameLength <= 50,
            })}
          >
            <p>!</p>
            <p>{nameLength}/50</p>
          </span>
        </div>
        <div className={styles.inputContainer}>
          <AuthInput
            type="text"
            label="Email"
            value={profile.email}
            onChange={(e) => {
              setUser({
                ...user,
                email: e.target.value,
              });
            }}
          />
        </div>
        <div className={styles.inputContainer}>
          <AuthInput
            type="password"
            label=""
            value=""
            placeholder=""
            onChange={(e) => {
              setUser({
                ...user,
                password: e.target.value,
              });
            }}
          />
        </div>
        <div className={styles.inputContainer}>
          <AuthInput
            type="password"
            label=""
            value=""
            placeholder=""
            onChange={(e) => {
              setUser({
                ...user,
                checkPassword: e.target.value,
              });
            }}
          />
        </div>
        <button className={styles.btn} onClick={handleSave}>
          
        </button>
      </div>
    </>
  );
};
