import { AuthInput } from "components/AuthInput";
import { ReactComponent as LogoSVG } from "assets/Icon.svg";
import { Link } from "react-router-dom";
import styles from "pages/LoginRegister.module.scss";
import { register } from "api/auth";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
export const Register = () => {
  const [account, setAccount] = useState("");
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [checkPassword, setCheckPassword] = useState("");
  const navigate = useNavigate();

  const handleRegister = async () => {
    if (account.length === 0) return;
    if (name.length === 0) return;
    if (email.length === 0) return;
    if (password.length === 0) return;
    if (checkPassword.length === 0) return;

    try {
      const success = await register({
        account,
        name,
        email,
        password,
        checkPassword,
      });
      console.log(success);
      if (success) {
        alert("");
        navigate("/login");
        return;
      }
    } catch (error) {
      alert("");
      return;
    }
  };

  return (
    <div className={styles.authContainer}>
      <div>
        <LogoSVG />
      </div>
      <h1></h1>
      <div className={styles.inputContainer}>
        <AuthInput
          type="text"
          value={account}
          onChange={(e) => setAccount(e.target.value)}
          label=""
          placeholder=""
          onKeyDown={handleRegister}
        />
      </div>
      <div className={styles.inputContainer}>
        <AuthInput
          type="text"
          value={name}
          onChange={(e) => setName(e.target.value)}
          label=""
          placeholder=""
          onKeyDown={handleRegister}
        />
      </div>
      <div className={styles.inputContainer}>
        <AuthInput
          type="text"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          label="Email"
          placeholder="Email"
          onKeyDown={handleRegister}
        />
      </div>
      <div className={styles.inputContainer}>
        <AuthInput
          type="password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          label=""
          placeholder=""
          onKeyDown={handleRegister}
        />
      </div>
      <div className={styles.inputContainer}>
        <AuthInput
          type="password"
          value={checkPassword}
          onChange={(e) => setCheckPassword(e.target.value)}
          label=""
          placeholder=""
          onKeyDown={handleRegister}
        />
      </div>

      <button className={styles.loginButton} onClick={handleRegister}>
        
      </button>
      <div className={styles.linkTextCancel}>
        <Link to="/login"></Link>
      </div>
    </div>
  );
};
